import { PureComponent } from "react";
import { Container } from "react-bootstrap";
import { Route } from "react-router-dom";
import AuthAPI from "./api/AuthAPI";
import ProfileAPI from "./api/ProfileAPI";
import Auth from "./components/Auth/Auth";
import Header from "./components/Header/Header";
import './index.scss';
import { ProfileType } from "./interface";

type AppStateType = {
    profile: ProfileType;
    init: boolean;
    isAuth: boolean | null;
}

class App extends PureComponent<{}, AppStateType> {
    state = {
        isAuth: null,
        profile: null,
        init: false,
    }
    async componentDidMount() {
        const initialize = async () => {
            const isAuthed = await AuthAPI.isAuth();
            if (!isAuthed) {
                this.setState({
                    init: true,
                    isAuth: false,
                    profile: null,
                });
                return 0;
            }
            const profile = await ProfileAPI.getMyProfile();
            this.setState({
                isAuth: isAuthed,
                profile,
                init: true,
            });
            return 0;
        }
        const resultCode = await initialize();
        if (resultCode === 0) {
            return this.setState(
                {
                    init: true
                }
            );
        } else {
            throw new Error(`Ошибка. ResultCode 1`)
        }

    }
    async authorize(dataObject: {
        login?: string;
        password?: string;
        vID?: number;
    }){
        if(dataObject.login && dataObject.password){
            await AuthAPI.Auth({
                login: dataObject.login,
                password: dataObject.password
            });
            return null;
        };
        if(dataObject.vID){
            await AuthAPI.AuthByVK(dataObject.vID);
            return null;
        }
    }
    render() {
        console.log('new 1');
        
        if (!this.state.init) {
            return <div>Loadingg</div>
        }
        return <>
            <Header profile={this.state.profile} />
            <main>
                <Container>
                    <Route path="/auth" exact render={() => <Auth onSubmited={this.authorize} />} />
                </Container>
            </main>
        </>
    }
}

export default App;