import axios, { AxiosInstance } from 'axios';
class Api {
    public instance: AxiosInstance = axios.create({
        baseURL: 'https://pacific-lowlands-57310.herokuapp.com/',
        headers: {
            api_key: 'MTpPzOlLBr4nJSAjeBzkATnb',
        },
        withCredentials: true
    });
    public async sendGET(...args: any[]): Promise<ApiRequestType>{
        const req = await this.instance.get(args[0], args[1]);
        const data: ApiRequestType = req.data;
        return data;
    }
    public async sendPOST(...args: any[]): Promise<ApiRequestType>{
        const req = await this.instance.post(args[0], args[1]);
        const data: ApiRequestType = req.data;
        return data;
    }
}

export type ApiRequestType = {
    data: any;
    errors: string[];
    statusCode: 0 | 1;
}

export default Api;