import { Button, TextField } from '@material-ui/core';
import { useFormik } from 'formik';
import { VKUser } from '../../interface';
import cssModule from './Auth.module.scss';
const reactVK = require('react-vk');
console.log(reactVK);

const VKAuth = reactVK.Auth;

type AuthProps = {
    onSubmited: (dataObject: {
        login?: string;
        password?: string;
        vID?: number;
    }) => void;
}
const Auth: React.FC<AuthProps> = (props) => {
    const formik = useFormik({
        initialValues: {
            login: "",
            password: "",
        },
        onSubmit: (values, actions) => {
            actions.resetForm();
            props.onSubmited({
                login: values.login,
                password: values.password
            });
        },
        validate: (values) => {
            const errors: any = {};
            if (!values.login) {
                errors.login = "Это поле обязательно!";
            }
            if (!values.password) {
                errors.password = "Это поле обязательно!";
            } else if (values.password.length < 7) {
                errors.password = "Минимальная длина пароля - 7 символов";
            }      
            return errors;
        }
    });
    return (
        <div className={cssModule["auth-page"]}>
            <form onSubmit={formik.handleSubmit}>
                <h1 className="mb-4">Авторизируйтесь!</h1>
                <div>
                    <TextField value={formik.values.login} error={formik.touched.login && Boolean(formik.errors.login)}
                        helperText={formik.touched.login && formik.errors.login} fullWidth onChange={formik.handleChange} label="Введите никнейм" variant="filled" name="login" />
                </div>
                <div className="mt-3">
                    <TextField value={formik.values.password} onChange={formik.handleChange} error={formik.touched.password && Boolean(formik.errors.password)}
                        helperText={formik.touched.password && formik.errors.password} fullWidth label="Введите пароль" name="password" variant="filled" type="password" />
                </div>
                <VKAuth options={{
                    onAuth: (users: VKUser[]) => {
                        const user = users[0];
                        console.log('start user', user);
                        
                        if(!user){
                            return null;
                        }
                        const VKUserID = user.uid;
                        console.log('VKUSERID', VKUserID);
                        
                        props.onSubmited({
                            vID: VKUserID
                        });
                    },
                    width: 300
                }}/>
                <Button className="mt-3" color="primary" variant="contained" fullWidth type="submit">
                    Авторизироваться
                </Button>
            </form>
        </div>
    )
}

export default Auth;