import Api, { ApiRequestType } from "./ApiClass";
import Cookie from 'js-cookie';
class AuthAPI extends Api {
    private theAuth: boolean | null = null;
    public async isAuth(){
        if((this.theAuth || !this.theAuth) && this.theAuth !== null){
            return this.theAuth;
        }
        const authKey = Cookie.get('authKey');
        if(!authKey){
            this.theAuth = false;
            return this.theAuth;
        }
        // если ключ авторизации есть(нужно проверить, действителен ли он. То есть, правда ли пользователь авторизирован)
        const json = await new Api().sendGET(`api/auth/checkAuth`); // отправляем запрос на сервер, где он проверяет, существует ли authKey куки в авторизированных.
        if(json.data === 0){
            // всё окей, юзер авторизирован
            this.theAuth = true; 
        } 
        if(json.data === 1){
            this.theAuth = false;
        }
        return this.theAuth;
    }
    public async Auth(data: {
        login: string;
        password: string;
    }){
        if(await this.isAuth()){
            return window.location.reload();
        }
        if(!data.login || !data.password){
            return window.location.reload();
        }
        const req = await this.sendGET(`api/auth/login?login=${data.login}&password=${data.password}`);
        const resultData = req.data;
        if(resultData === 0){
            // авторизация прошла успешно
            console.log('Авторизация успешна!');
        } else if(resultData === 1){
            // авторизация не успешна
            const error = req.errors[0];
            console.log('Авторизация не успешна', error)
        }
    }
    public async AuthByVK(userID: number){
        console.log('AuthBYVK', userID);
        
        if(await this.isAuth()){
            return window.location.reload();
        }
        if(!userID){
            return window.location.reload();
        }
        console.log('перед req');
        
        const req = await this.sendPOST(`api/auth/vk_login`, {
            body: {
                userID
            }
        });
        console.log('после req', req);
        
        const resultData = req.data;
        if(resultData === 0){
            // авторизация прошла успешно
            console.log('Авторизация успешна!');
        } else if(resultData === 1){
            // авторизация не успешна
            const error = req.errors[0];
            console.log('Авторизация не успешна', error)
        }
    }
}

export default new AuthAPI();