import { Button } from "@material-ui/core";
import { Component } from "react";
import { Container } from "react-bootstrap";
import logo from '../../assets/images/logo.png';
import { ProfileType } from "../../interface";
import {Link} from 'react-router-dom';
import './Header.scss';
import NoNameAvatar from '../../assets/images/avatar.png';
class Header extends Component<{
    profile: ProfileType;
}, {}> {
    render() {
        return <header>
            <Container fluid="sm">
                <nav className="header__nav">
                    <div className="header__element-logo">
                        <img src={logo} alt="logo" />
                    </div>
                    <div className="central-title">Министерство Здравоохранения</div>
                    <div>
                        {this.props.profile ? (
                            <div className="btn-group">
                            <button className="btn btn-secondary btn-lg dropdown-toggle header__profile" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                <div className="header__profile-avatar">
                                    <img src={this.props.profile.avatar ? this.props.profile.avatar : NoNameAvatar} alt="profile__avatar" />
                                </div>
                                <div className="header__profile-nickname">
                                    {this.props.profile.nickName}
                                </div>
                            </button>
                            <ul className="dropdown-menu">
                                <li><a className="dropdown-item" href="#">Архивы</a></li>
                                <li><a className="dropdown-item" href="#">Больницы</a></li>
                                <li><a className="dropdown-item" href="#">Мой профиль</a></li>
                                <li><hr className="dropdown-divider" /></li>
                                <li><a className="dropdown-item" href="#">Выйти</a></li>
                            </ul>
                        </div>
                        ) : <Link to="/auth" className="text-decoration-none"><Button variant="contained" color="secondary">
                        Войти
                      </Button></Link>}
                    </div>
                </nav>
            </Container>
        </header>
    }
}

export default Header;